import './App.css';
import React, { useEffect, useState } from 'react';
import _ from 'underscore';
import { Button, Container, Row, Col, Form } from 'react-bootstrap';
import { Bar, BarChart, ResponsiveContainer, LineChart, Label, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

function App() {
  return (
    <Container fluid>
      <header className="jumbotron py-2 my-2">
        <h1 className="text-center">SGraphs: a data explorer for data.gov.sg</h1>
        <h6 className="text-center">react.js visualisation tool using publicly available Singapore Government data</h6>
      </header>
      <GovDataChart />
      <footer className="py-2 text-center">
        Note: you may encounter errors with certain datasets due to 404 errors, or access restrictions put in place by data.gov.sg.
        <br />
        Data Visualisation by Nathaniel Wong. Contains information from datasets accessed from data.gov.sg which is made available under the terms of the Singapore Open Data Licence version 1.0.
      </footer>
    </Container >

  );
}



function ChartSettings(props) {

  let [xMin, setXMin, xMax, setXMax, yMin, setYMin, yMax, setYMax] = props.domain;
  let [resourceID, setResourceID] = props.resourceID;
  let [xKey, setXKey, yKey, setYKey, series, setSeries] = props.keys;
  let [sumData, setSumData] = props.sumData;
  let fields = props.fields ? Object.values(props.fields).splice(1).map(item => item.id) : ['Loading...'];
  let [limit, setLimit] = props.limit;
  let [organisation, setOrganisation] = props.organisation;

  const [datasetMenu, setDatasetMenu] = useState([]);

  function getResourceNamefromID(resID) {
    let resource = Object.values(props.packages.filter(item => item.resource_id === resID))[0];
    if (!resource) return '';
    return resource.resource_name;
  }


  useEffect(() => {
    let menu = props.filterResourceIDs(props.packages, organisation);
    setDatasetMenu(menu);
  }, [organisation, props.packages, resourceID]);

  return (
    <Form className="settingsbar px-3 py-3">
      <Form.Group className="mb-3" controlId="dataset">
        <Row><h3 className="mb-3">Dataset Selection</h3></Row>
        <Row>
          <Col xs={4}><Form.Label>Organisation</Form.Label></Col>
          <Col><Form.Select value={organisation} onChange={(e) => setOrganisation(e.target.value)}>
            {props.orgList.map(key => (
              <option key={key} value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={4}><Form.Label>Dataset</Form.Label></Col>
          <Col><Form.Select value={resourceID} onChange={(e) => setResourceID(e.target.value)}>
            {datasetMenu.map(key => (
              <option key={key} value={key}>{getResourceNamefromID(key)}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={4}><Form.Label>No. of Entries</Form.Label></Col>
          <Col><Form.Control type="text" defaultValue={limit} onChange={(e) => setLimit(e.target.value)} /></Col>
        </Row>
      </Form.Group>
      <Form.Group className="mb-3" controlId="dataset">
        <Row><h3 className="mb-3">Data Manipulation</h3></Row>
        <Row>
          <Col xs={4}><Form.Label>X Axis</Form.Label></Col>
          <Col><Form.Select value={xKey} onChange={(e) => setXKey(e.target.value)}>
            {fields.map(key => (
              <option key={key} value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={4}><Form.Label>Y Axis</Form.Label></Col>
          <Col><Form.Select value={yKey} onChange={(e) => setYKey(e.target.value)}>
            {fields.map(key => (
              <option key={key} value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={4}><Form.Label>Series</Form.Label></Col>
          <Col><Form.Select value={series} onChange={(e) => setSeries(e.target.value)}>
            {fields.map(key => (
              <option key={key} value={key}>{key}</option>
            ))}
          </Form.Select></Col>
        </Row>
        <Row>
          <Col xs={6}><Form.Label>Sum Y-axis data entries?</Form.Label></Col>
          <Col><Form.Check type="checkbox" checked={sumData} onChange={(e) => setSumData(!sumData)} /></Col>
        </Row>
      </Form.Group>
      <Form.Group className="mb-3" controlId="xyAxisDomain">
        <Row><h3 className="mb-3">Chart Range</h3></Row>
        <Row>
          <Col><Form.Text>Min X</Form.Text> </Col>
          <Col><Form.Control type="text" defaultValue={xMin} onChange={(e) => setXMin(e.target.value)} /></Col>
          <Col><Form.Text>Max X</Form.Text> </Col>
          <Col><Form.Control type="text" defaultValue={xMax} onChange={(e) => setXMax(e.target.value)} /></Col>
        </Row>
        <Row>
          <Col><Form.Text>Min Y</Form.Text></Col>
          <Col><Form.Control type="text" defaultValue={yMin} onChange={(e) => setYMin(e.target.value)} /></Col>
          <Col><Form.Text>Max Y</Form.Text></Col>
          <Col><Form.Control type="text" defaultValue={yMax} onChange={(e) => setYMax(e.target.value)} /></Col>
        </Row>
        <Row>
          <Form.Text>Valid inputs: a number, 'auto', 'dataMin', 'dataMax', or a string like 'dataMin - 20', 'dataMax + 100'</Form.Text>
        </Row>
      </Form.Group>
      <Row>
        <Col xs={6}>
          <Button target="_blank" href={"https://data.gov.sg/search?q=" + getResourceNamefromID(resourceID)}>View on data.gov.sg</Button>
        </Col>
      </Row>
    </Form >
  );
}



function SimpleLineChart(props) {

  let [xMin, xMax, yMin, yMax] = props.domain;

  function parseFloatOrText(value) {
    let int = parseFloat(value);
    return isNaN(int) ? value : int;
  }

  function getRandomDarkColor() {
    let color = "#";
    for (let i = 0; i < 6; i++) {
      color += Math.floor(Math.random() * 10);
    }
    return color;
  }

  function isAxisNumerical(key) {
    if (!Object.keys(props.dataset)) return false;
    let first_series = Object.keys(props.dataset)[0];
    let val = props.dataset[first_series][0][key];
    if (!val) return false;
    return /^-?\d*(\.\d+)?$/.test(val);
  }

  if (props.error) return <div>Error: {props.error.message}</div>;
  if (!props.isLoaded) return <div>Loading Data...</div>;

  return (
    <ResponsiveContainer width="100%" height={600}>
      <LineChart>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis type={isAxisNumerical(props.xKey) ? "number" : "category"} dataKey={props.xKey} domain={[parseFloatOrText(xMin), parseFloatOrText(xMax)]}>
          <Label value={props.xKey} offset={0} position="insideBottom" />
        </XAxis>
        <YAxis type={isAxisNumerical(props.yKey) ? "number" : "category"} interval="preserveStartEnd" allowDataOverflow={true} domain={[parseFloatOrText(yMin), parseFloatOrText(yMax)]}>
          <Label value={props.yKey} angle={-90} offset={0} position="insideLeft" />
        </YAxis>
        <Tooltip filterNull="true" itemStyle={{ fontSize: '0.7em', padding: 0 }} />
        <Legend iconSize={9} wrapperStyle={{ fontSize: '0.5em' }} />
        {Object.keys(props.dataset).map(key => (
          <Line type="natural" legendType="square" activeDot={{ r: 6 }} dataKey={props.yKey} data={props.dataset[key]} name={key} key={key} stroke={getRandomDarkColor()} />
        ))}
      </LineChart>
    </ResponsiveContainer>
  );

}



function SimpleBarChart(props) {

  if (props.error) return <div>Error: Could not retrieve dataset</div>;
  if (!props.isLoaded) return <div>Loading Data...</div>;

  return (
    <BarChart>
      <Bar data={Object.values(props.dataset)[0]} dataKey="amount" />
    </BarChart>
  );
}



function GovDataChart(props) {
  const [packages, setPackages] = useState([]);
  const [orgList, setOrgList] = useState([]);
  const [organisation, setOrganisation] = useState('');
  const [resourceID, setResourceID] = useState('');
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [result, setResult] = useState([]);
  const [sumData, setSumData] = useState(true);
  const [limit, setLimit] = useState('1000');
  const [xMin, setXMin] = useState('auto');
  const [xMax, setXMax] = useState('auto');
  const [yMin, setYMin] = useState('auto');
  const [yMax, setYMax] = useState('auto');
  const [xKey, setXKey] = useState('');
  const [yKey, setYKey] = useState('');
  const [series, setSeries] = useState('');
  const [dataset, setDataset] = useState([]);

  // retrieve package list when page loads.
  useEffect(() => {
    setIsLoaded(false);
    let url = "https://data.gov.sg/api/action/datastore_search?resource_id=85be5dcc-93f6-4d36-ae10-c85b0907948c&limit=10000";
    url += "&offset=" + "0";
    fetch(url).then(res => res.json()).then((res) => {
      setError(null);
      let filteredPackages = res.result.records.filter(item => item.resource_format === 'CSV');
      setPackages(filteredPackages);
      let newOrgList = [...new Set(filteredPackages.map(item => item.organisation))].sort()
      setOrgList(newOrgList);
      let newOrg = newOrgList[Math.floor(Math.random() * newOrgList.length)];
      setOrganisation(newOrg);

    });
  }, []);

  // update datasets whenever there is a change in organisation selection
  useEffect(() => {
    let resourceIDs = filterResourceIDs(packages, organisation);
    let newResourceID = resourceIDs[Math.floor(Math.random() * resourceIDs.length)];
    setResourceID(newResourceID);
  }, [packages, organisation]);

  // retrieve dataset based on resourceID
  useEffect(() => {
    if (!resourceID) return;
    let url = "https://data.gov.sg/api/action/datastore_search?";
    url += "&limit=" + limit;
    url += "&resource_id=" + resourceID;
    fetch(url).then(res => res.json()).then(
      (res) => {
        if (!res.success) {
          setError({ message: "API error, resource not available" });
          let newOrg = orgList[Math.floor(Math.random() * orgList.length)];
          setOrganisation(newOrg);
        }
        else {
          // store result
          setError(null);
          setResult(res.result);
          // set default x, y and series values
          let temp_x, temp_y, temp_series = null;
          let fields = res.result.fields;
          for (let item in fields) {
            let field_name = fields[item].id;
            let sample_data = res.result.records[0][field_name];
            if (field_name === '_id') continue;
            if (!temp_x || (temp_y && !isFloatOrInt(res.result.records[0][temp_x]) && isFloatOrInt(sample_data)) || field_name.includes('year') || field_name.includes('month')) {
              setXKey(field_name);
              temp_x = field_name;
              continue;
            }
            if ((!temp_y && isFloatOrInt(sample_data)) || field_name.includes('value') || field_name.includes('amt') || field_name.includes('amount')) {
              setYKey(field_name);
              temp_y = field_name;
              continue;
            }
            if (!temp_series) {
              let new_series = res.result.records.map(item => item[field_name]);
              let new_unique = [...new Set(new_series)];
              if (new_unique.length > 20) continue;
              setSeries(field_name);
              temp_series = field_name;
            } else {
              let new_series = res.result.records.map(item => item[field_name]);
              let old_series = res.result.records.map(item => item[temp_series]);
              let new_unique = [...new Set(new_series)];
              let old_unqiue = [...new Set(old_series)];
              if (new_unique.length < old_unqiue.length) setSeries(field_name);
            }
            if (temp_x && temp_y && temp_series) break;
          }
        }
        setIsLoaded(true);
      },
      (error) => {
        setError(error);
        if (!isLoaded) {
          let newOrg = orgList[Math.floor(Math.random() * orgList.length)];
          setOrganisation(newOrg);
        }
      }
    );
  }, [resourceID, limit, organisation]);

  // update datasets whenever there are changes to the parameters
  useEffect(() => {
    if (!result.records) return;
    let sorted_records = result.records.sort((a, b) => a[xKey] > b[xKey]);

    if (!sumData) {
      // if we aren't summing data, a GroupBy opreation will suffice
      setDataset(_.groupBy(sorted_records, series));
      return;
    }
    let set = {};
    for (const i in sorted_records) {
      let item = sorted_records[i];
      let seriesID = item[series];
      if (!set[seriesID]) {
        // series doesnt exist yet, insert first data point
        set[seriesID] = [{ [xKey]: item[xKey], [yKey]: parseFloatOrText(item[yKey]) }];
        continue;
      }
      // series exists,  check if xKey exists in series
      let found = false;
      for (const j in set[seriesID]) {
        if (set[seriesID][j][xKey] === item[xKey]) {
          found = true;
          // item is NaN, don't even bother with it
          if (isNaN(parseFloat(item[yKey]))) break;
          // replace NaN with number if possible
          if (isNaN(parseFloat(set[seriesID][j][yKey]))) {
            set[seriesID][j][yKey] = parseFloatOrText(item[yKey]);
          }
          // both are numbers! lets add y Values together IF both are numbers
          set[seriesID][j][yKey] += parseFloatOrText(item[yKey]);
          break;
        }
      }
      // x-key doesn't exist, push it to dataset
      if (!found) set[seriesID].push({ [xKey]: item[xKey], [yKey]: parseFloatOrText(item[yKey]) });

    }
    setDataset(set);
  }, [sumData, result, series, xKey, yKey]);

  function filterResourceIDs(pkgs, org) {
    return [... new Set(pkgs.filter(item => (item.organisation === org)).map(item => item.resource_id))];
  }

  function parseFloatOrText(value) {
    let int = parseFloat(value);
    return isNaN(int) ? value : int;
  }

  return (
    <Row className="justify-items-center">
      <Col xs={12} md={6} lg={8}>
        <Container className="justify-items-center">
          <SimpleLineChart isLoaded={isLoaded} error={error} dataset={dataset} xKey={xKey} yKey={yKey} domain={[xMin, xMax, yMin, yMax]} />
        </Container>
      </Col>
      <Col xs={12} md={6} lg={4}>
        <ChartSettings
          keys={[xKey, setXKey, yKey, setYKey, series, setSeries]}
          domain={[xMin, setXMin, xMax, setXMax, yMin, setYMin, yMax, setYMax]}
          resourceID={[resourceID, setResourceID]}
          fields={result.fields}
          sumData={[sumData, setSumData]}
          limit={[limit, setLimit]}
          packages={packages}
          orgList={orgList}
          organisation={[organisation, setOrganisation]}
          filterResourceIDs={filterResourceIDs}
        />
      </Col>

    </Row>
  );
}

function isFloatOrInt(val) {
  return /^-?\d*(\.\d+)?$/.test(val);
}



export default App;
